div,input,textarea,button{box-sizing:border-box}body{background:linear-gradient(#00bef0, #4fd2dc) no-repeat;height:100vh;font-family:helvetica}.emailModal{background-color:#fff;border-radius:5px;margin:0 auto;max-width:655px;overflow:hidden;width:100%}.emailModal__content{padding:15px}.emailModal__field,.emailModal__inputField{border:1px solid #e0e0e0;border-radius:4px;margin:5px 0;padding:5px;width:100%}.emailModal__inputField{min-height:185px;overflow:auto}.emailSent{display:hidden}.emailSent hr{width:80%;margin:0 auto}.emailSent .fa-check-circle{color:#4bd59c;font-size:6rem;text-align:center}.emailSent i{margin:0 auto}.emailHead{background-color:#094358;color:#fff;font-size:1.5rem;height:80px;line-height:80px;padding:0 15px}.sendButton{border:none;border-radius:18px;font-weight:700;height:35px;width:100px;color:#a1a2a5}.attachButton{background-color:#fff;border-radius:50%;border:1px solid #e0e0e0;color:#a1a2a5;cursor:pointer;font-size:1.5rem;height:35px;width:35px}.activeSend{background-color:#04b7e5;cursor:pointer;color:#fff}@media only screen and (max-width: 768px){.emailModal{width:calc(100% - 20px)}}
